import {
	addCollection,
	getCollection,
	delCollection,
	addApply,
	getApply,
	getJobDetail,
	getJob,
} from '@/api/job'
import {
	$wuxToast
} from '../../wxcomponents/wux/index'
import QQMapWX from '../../common/qqmap-wx-jssdk.min.js';
var qqmapsdk;
export default {
	data() {
		return {
			det: true,
			collection: false,
			apply: false,
			detail: {
				company: {
					nature: 1,
					size: 1,
				},
				salarystart: 1,
				salaryend: 1,
				schooling: 1,
				pubdate: 1571801261,
			},
			job: {
				list: null,
				search: {
					page: 1,
					limit: 5
				}
			},
			open: true,
		}
	},
	onShow() {
		if (this.$store.getters.register && this.detail.id) {
			this.getCollection()
			this.getApply()
		}
		if (this.$store.getters.member.ac < 2) {
			this.$store.dispatch('MemberAC')
		}
	},
	onLoad(e) {
		if (e.det === 'stop') {
			this.det = false
		}
		this.getJobDetail(e.id)
		qqmapsdk = new QQMapWX({
			key: 'I3ABZ-QPZW3-JIP3T-3AVMR-TRALT-77BFQ'
		});
	},
	onPullDownRefresh() {
		this.refresher()
		uni.stopPullDownRefresh();
	},
	onReachBottom() {
		this.refresher()
	},
	methods: {
		getJobDetail(id) {
			getJobDetail({
				id
			}).then(response => {
				let r = response.data
				if (response.code === 20000) {
					this.detail = r.detail
					if (this.detail.info.tag) {
						this.detail.info.tag = r.detail.info.tag.split(',')
					}
					if (this.$store.getters.register && this.detail.id) {
						this.getCollection()
						this.getApply()
					}
					if (this.det) {
						this.getJob()
					}
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
				}
			})
		},
		getJob() {
			const s = this.job.search
			const t = this.job
			const a = this.$store.getters.address
			const search = Object.assign({}, {
					page: s.page,
					limit: s.limit,
				},
				a.province ? {
					province: a.province
				} : {},
				a.city ? {
					city: a.city
				} : {},
				a.area ? {
					area: a.area
				} : {},
			);
			getJob(search).then(response => {
				let r = response.data
				if (response.code === 20000) {
					t.list = r.list
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
				}
			})
		},
		getCollection() {
			getCollection({
				id: this.detail.id
			}).then(response => {
				let r = response.data
				if (response.code === 20000) {
					this.collection = r.detail
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
				}
			})
		},
		delCollection(e) {
			if (this.$store.getters.register) {
				delCollection({
					id: this.detail.id
				}).then(response => {
					let r = response.data
					if (response.code === 20000 && r.del) {
						this.collection = false
						$wuxToast().show({
							type: 'success',
							text: '已取消收藏'
						})
					} else {
						$wuxToast().show({
							type: 'forbidden',
							text: '请求异常'
						})
					}
				})
			} else {
				// 跳转授权注册页面
				uni.navigateTo({
					url: '/pages/login/wx'
				});
			}
		},
		getApply() {
			getApply({
				id: this.detail.id
			}).then(response => {
				let r = response.data
				if (response.code === 20000) {
					this.apply = r.detail
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
				}
			})
		},
		addCollection(e) {
			if (this.$store.getters.register) {
				addCollection({
					id: this.detail.id
				}).then(response => {
					let r = response.data
					if (response.code === 20000 && r.add) {
						this.collection = true
						$wuxToast().show({
							type: 'success',
							text: '收藏成功'
						})
					} else if (response.code === 20000 && r.detail) {
						this.collection = true
						$wuxToast().show({
							type: 'success',
							text: '已收藏'
						})
					} else {
						$wuxToast().show({
							type: 'forbidden',
							text: '请求异常'
						})
					}
				})
			} else {
				// 跳转授权注册页面






				uni.navigateTo({
					url: '/pages/login/wx'
				});

			}
		},
		addApply(e) {
			if (this.$store.getters.register) {
				if (this.$store.getters.member.ac > 1) {
					addApply({
						id: this.detail.id
					}).then(response => {
						let r = response.data
						if (response.code === 20000 && r.add) {
							this.apply = true
							$wuxToast().show({
								type: 'success',
								text: '投递成功'
							})
						} else if (response.code === 20000 && r.detail) {
							this.apply = true
							$wuxToast().show({
								type: 'success',
								text: '已投递'
							})
						} else {
							$wuxToast().show({
								type: 'forbidden',
								text: '请求异常'
							})
						}
					})
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '账号未认证'
					})
					setTimeout(() => {
						uni.navigateTo({
							url: '/pages/my/ac'
						});
					}, 1000)
				}
			} else {






				uni.navigateTo({
					url: '/pages/login/wx'
				});

			}
		},
		detailTo(i, n) {
			if (n === 1) {
				uni.navigateTo({
					url: '/pages/company/detail?det=stop&id=' + i
				});
			} else if (n === 2) {
				uni.navigateTo({
					url: '/pages/job/detail?det=stop&id=' + i
				});
			} else if (n === 3) {
				uni.navigateTo({
					url: '/pages/archives/detail?id=' + i
				});
			}
		},
		openMap() {
			let o = this.open
			if (o) {
				o = false
				const d = this.detail
				qqmapsdk.geocoder({
					address: d.province + d.city + d.area + d.info.address,
					success: res => {
						const location = res.result.location
						uni.openLocation({
							latitude: location.lat,
							longitude: location.lng,
							success: function() {
								setTimeout(() => {
									o = true
								}, 2000)
							},
							fail: function() {
								o = true
							},
						});
					}
				});
			}
		}
	}
}
